<template>
  <div class="all">

    <div class="container">

      <h2 class="web-title align-center">相册目录</h2>
      <h3 class="web-describe align-center">摄影让我发现生活的美好，博客记录下美好的记忆</h3>

      <div class="clear photoListBox">
        <div class="lists fl" v-for="(item,index) in lists" :key="index" @click="goDetail(item.id)">
          <div class="boxshow">
              <div class="cover" :style="{backgroundImage: 'url(' + item.imgurl + ')'}">
                <div class="desc">
                  <p class="title">{{item.describe}}</p>
                </div>
              </div>
              <p class="describe wordbreak">{{item.title}}</p>
              <p class="visittime"><i class="iconfont icon-shijian"></i><span class="time">{{item.time}}</span></p>
          </div>
        </div>
        <div class="copyright">
          <p>© 2018 | Eliden</p>
        </div>
      </div>

    </div>
  </div>
</template>

<script>

export default {
  name: 'Index',
  data () {
    return {
      lists: [
        {id: '1', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2ZTS.jpg'},
        {id: '2', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2Eef.jpg'},
        {id: '3', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2kOP.jpg'},
        {id: '4', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2Fyt.jpg'},
        {id: '4', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2Fyt.jpg'},
        {id: '4', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2Fyt.jpg'},
        {id: '4', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2Fyt.jpg'},
        {id: '4', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2Fyt.jpg'},
        {id: '4', title: '111', describe: 'gsrgidhgdnhsgirdgiudhrighdrig', time: '2018-08-08', imgurl: 'https://s1.ax1x.com/2018/09/07/iC2Fyt.jpg'}
      ]
    }
  },
  methods: {
    goDetail (id) {
      this.$router.push({name: 'photoDetail', query: {id: id}})
    }
  }
}
</script>

<style scoped lang="stylus">
  .all
    width: 100%
    color: #8f989f;
    background-color: #F4F7F7
    font-size: .2rem
    font-family: "Roboto Mono", monospace;
    font-weight: 400;
    line-height: 1.8;

  .web-title
    font-size: .3rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: .12em;
    color: #000
    padding-top:.6rem
  .web-describe
    font-size: .18rem
    padding: .3rem 1rem 0
    word-break break-all
  .photoListBox
    position: relative
    padding-bottom:1.2rem

  .lists
    padding:0 .15rem
    width: 33.333%
    margin-top:.6rem
.boxshow
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.2)
.cover
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: relative;
    color: #8f989f;
    height: 3rem
    width: 100%

.desc
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.95);
    opacity: 0;
    padding: .3rem;
    transition: 0.3s;
    color: #8f989f;
    .title
      word-break break-all
      font-size: .22rem

.lists:hover .desc
    opacity: 0.8
.lists:hover .boxshow
  box-shadow: 0px 10px 18px -4px rgba(0, 0, 0, 0.3);

.describe
    width: 100%
    text-align: center
    padding:.18rem
    color: #000;
    font-weight: 400;
  .visittime
    border-top: 1px solid rgba(0, 0, 0, 0.05);
    padding:.14rem
    text-align: center
    font-size:.16rem
    margin:0 .2rem
    .time
      margin-left:.1rem
</style>



